@import"https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@400;500;700;900&family=Outfit:wght@400;600;800&display=swap";@tailwind base;@tailwind components;@tailwind utilities;:root{--theme-color: #FF9EAA;--theme-color-rgb: 255, 158, 170;--theme-color-light: rgb(255, 195, 203);--theme-color-dark: rgb(204, 126, 136);--base-font-size: 16px;--min-height: 640px;--color-primary: var(--theme-color);--color-secondary: #3AA6B9;--color-accent: #C1ECE4;--color-dark: #1A1A2E;--color-glass: rgba(255, 255, 255, 0.25);--color-glass-border: rgba(255, 255, 255, 0.3);--font-sans: 'Outfit', 'Noto Sans SC', sans-serif}*{box-sizing:border-box}body{margin:0;font-family:var(--font-sans);font-size:var(--base-font-size);background:rgba(0,0,0,0)}#app{width:100%;height:100vh;min-height:var(--min-height)}::-webkit-scrollbar{width:6px}::-webkit-scrollbar-track{background:hsla(0,0%,100%,.1)}::-webkit-scrollbar-thumb{background:rgba(236,72,153,.5);border-radius:3px}::-webkit-scrollbar-thumb:hover{background:rgba(236,72,153,.8)}@keyframes glitch{0%{transform:translate(0)}20%{transform:translate(-2px, 2px)}40%{transform:translate(-2px, -2px)}60%{transform:translate(2px, 2px)}80%{transform:translate(2px, -2px)}100%{transform:translate(0)}}.glitch-text:hover{animation:glitch .3s cubic-bezier(0.25, 0.46, 0.45, 0.94) both infinite;color:#f472b6}.scanlines{background:linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0) 50%, rgba(0, 0, 0, 0.1) 50%, rgba(0, 0, 0, 0.1));background-size:100% 4px;pointer-events:none}.text-theme{color:var(--theme-color) !important}.bg-theme{background-color:var(--theme-color) !important}.bg-theme-light{background-color:var(--theme-color-light) !important}.bg-theme-dark{background-color:var(--theme-color-dark) !important}.border-theme{border-color:var(--theme-color) !important}.glass-panel{background:hsla(0,0%,100%,.65);backdrop-filter:blur(16px);-webkit-backdrop-filter:blur(16px);border:1px solid rgba(148,163,184,.25);box-shadow:0 8px 32px 0 rgba(31,38,135,.15)}.main-glass-card{background:linear-gradient(135deg, rgba(255, 250, 252, 0.95) 0%, rgba(255, 235, 240, 0.9) 100%);backdrop-filter:blur(24px);-webkit-backdrop-filter:blur(24px);border:1px solid rgba(148,163,184,.2);box-shadow:0 10px 40px -10px rgba(255,182,193,.3),0 0 0 1px hsla(0,0%,100%,.8) inset,0 1px 3px rgba(0,0,0,.05)}.glass-btn{background:hsla(0,0%,100%,.2);backdrop-filter:blur(4px);border:1px solid hsla(0,0%,100%,.3);transition:all .3s cubic-bezier(0.4, 0, 0.2, 1)}.glass-btn:hover{background:hsla(0,0%,100%,.4);transform:translateY(-2px);box-shadow:0 4px 12px rgba(0,0,0,.1)}.custom-scrollbar::-webkit-scrollbar{width:6px;height:6px}.custom-scrollbar::-webkit-scrollbar-track{background:rgba(0,0,0,.05);border-radius:3px}.custom-scrollbar::-webkit-scrollbar-thumb{background:rgba(255,182,193,.4);border-radius:3px}.custom-scrollbar::-webkit-scrollbar-thumb:hover{background:rgba(255,182,193,.6)}.scrollbar-hide{-ms-overflow-style:none;scrollbar-width:none}.scrollbar-hide::-webkit-scrollbar{display:none}::selection{background:rgba(255,158,170,.3);color:#831843}@keyframes float{0%,100%{transform:translateY(0)}50%{transform:translateY(-10px)}}@keyframes slideUp{0%{opacity:0;transform:translateY(20px)}100%{opacity:1;transform:translateY(0)}}@keyframes slideLeft{0%{opacity:0;transform:translateX(30px)}100%{opacity:1;transform:translateX(0)}}@keyframes pulse-slow{0%,100%{opacity:1}50%{opacity:.5}}@keyframes fadeIn{from{opacity:0;transform:translateY(10px)}to{opacity:1;transform:translateY(0)}}.animate-float{animation:float 6s ease-in-out infinite}.animate-pulse-slow{animation:pulse-slow 4s cubic-bezier(0.4, 0, 0.6, 1) infinite}.animate-slide-up{animation:slideUp .5s ease-out forwards}.animate-slide-left{animation:slideLeft .5s cubic-bezier(0.2, 0.8, 0.2, 1) forwards}.loading-spinner{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100%;min-height:var(--min-height);gap:16px;color:var(--theme-color)}